	import uniRate from "../../components/uni-rate/uni-rate.vue";
    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default { 
		data() {
			return {
				orderlist:[],
				datalist:[],
				loadingText: '正在加载...',
				hasmore:true,
				curpage:1,
				showtype:0
			}				
		},
		components: {uniRate},
        computed: mapState(['hasLogin','userInfo']),  
		onLoad() {
			console.log('页面加载');
			if (!this.hasLogin) {  
				uni.navigateTo({
					url:'login',
				});
				return false;
			}
			this.loadData();
		},
		  onBackPress(options) {
			  console.log('==onBackPress===');
			
		  },
         onUnload() {
             console.log('页面卸载');
         },
         onReachBottom() {
             console.log('正在加载中...');
			 this.curpage++;
			 this.loadData();
         },
         onPullDownRefresh() {
             uni.stopPullDownRefresh();
         },
        methods: {   
            async loadData() {  
				var _this = this;
                if (!this.hasLogin) {  
					uni.navigateTo({
						url:'login',
					})
                }else{
					if(this.showtype==1){
						this.showPinLunList();
					}else if(this.showtype==0){
						this.showOrderList();
					}
					
				}
            },
			showOrderList(){
				let _this = this;
				if(!this.hasmore){
					this.loadingText = '暂无更多信息';
					return false;
				}
				_this.$Request.post('member_evaluate/orderlist',{curpage:_this.curpage}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.hasmore =res.hasmore;
						console.log(_this.hasmore);
						let _orderlist = res.datas.order_list;
						for(var i=0;i<_orderlist.length;i++){
							_this.orderlist.push(_orderlist[i]);
						}
						if(!res.hasmore){
							_this.loadingText = '暂无更多信息';
						}
					}
				});
			},
			showPinLunList(){
				let _this = this;
				if(!this.hasmore){
					this.loadingText = '暂无更多信息';
					return false;
				}
				_this.$Request.post('member_evaluate/list',{curpage:_this.curpage}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.hasmore =res.hasmore;
						console.log(_this.hasmore);
						let _datalist = res.datas.eval_list;
						for(var i=0;i<_datalist.length;i++){
							_this.datalist.push(_datalist[i]);
						}
						if(!res.hasmore){
							_this.loadingText = '暂无更多信息';
						}
					}
				});
			},
			toEval: function(id) {
				uni.navigateTo({
					url:'comment?id='+id,
				})
			},
			showTab(t){
				uni.hideLoading();
				this.showtype=t;
				this.hasmore=true;
				this.loadingText = '正在加载...';
				this.datalist=[];
				this.orderlist=[];
				this.loadData();
			},
			previewImg(src,urls){
				uni.previewImage({
					current:src,
					urls
				})
			},
			deleteEval(id){
				var _this= this;
				
				uni.showModal({
					title: '提示',
					content: '确认要删除吗？',
					success: function (re) {
						if (re.confirm) {
							uni.showLoading({title:'正在删除...'});
							_this.$Request.post('member_evaluate/eval_delete',{eval_id:id}).then(res => {
								if (res.code == 200) {
									console.log(res);
									let len = _this.datalist.length;
									for(let i=0;i<len;i++){
										if(id==_this.datalist[i].seval_id){
											_this.datalist.splice(i, 1);
											break;
										}
									}
									uni.hideLoading();
								}
								else{
									uni.showLoading({title:res.datas.error+''});
									setTimeout(function () {
										uni.hideLoading();
									}, 1500);
								}
							});
						} else if (re.cancel) {
							console.log('用户点击取消');
						}
					}
				});
				
			}
			 
        }  
    }  
